
@import url("//unpkg.com/element-ui@2.0.4/lib/theme-chalk/index.css");

/* oldlace */
.el-table .free {
    background: #f0f9eb;
}


/* green */
.el-table .premium {
    background: #fdf5e6;
}


/* black */
.el-table .purchased {
    background: #e3e3e3;
}


/* for selected row */
.el-table .current-row {
    background: none;
}



